<template>
    <yd-layout class="acrossSystem">
        <div class="acrossSystem">
            <div class="fuyan" v-if="fuyanData!=''">
                <div class="fuyan_title">附言：</div>
                <div class="fuyan_text">{{fuyan}}</div>
            </div>
            <div class="approveTitle">
                <div class="title" v-text="formData.reqname"></div>
                <div class="approveType" v-text="formData.approvalitem"></div>
            </div>
            <div class="docDepart">
                <div class="txt">
                    <ul>
                        <li @click="textFlag=1">
                            <img src="../../../static/images/label/label_txt.png" class="li_img">
                            <p>内容概要</p>
                        </li>
                        <li>
                            <img src="../../../static/images/label/label_pdf_shadow.png" class="li_img" v-show="zwTitleArr.length==0">
                            <img src="../../../static/images/label/label_pdf.png" class="li_img" @click="textFlag=2" v-show="zwTitleArr.length>0">
                            <p> 正 文</p>
                        </li>
                        <li>
                            <img src="../../../static/images/label/label_doc.png" class="li_img" v-show="fjTitleArr.length>0" @click="textFlag=3">
                            <img src="../../../static/images/label/label_doc_shadow.png" class="li_img" v-show="fjTitleArr.length==0">
                            <p> 附 件</p>
                        </li>
                    </ul>
                </div>
                <div class="abstract" :class="{'abstract_hei':openFlag==true}" v-show="textFlag==1">
                    <p class="ab_p" v-show="abstract.length>59" v-html="abstract.length>59&&openFlag==false?transferString(abstract.substring(0,59))+'...':transferString(abstract)"></p>
                    <p class="ab_p" ref="ab_p" v-show="abstract.length<=59" v-html="transferString(abstract)"></p>
                    <span class="ab_span" v-show="abstract.length>59" @click="openFlag=!openFlag">{{openFlag==true?'收起概要':'展开全部'}}</span>
                </div>
                <div class="zw" v-show="textFlag==2" :class="{'abstract_hei':zwFlag==true}">
                    <div class="fjTitle" v-show="zwFlag==true?index<zwTitleArr.length:index<3" v-for="(item, index) in zwTitleArr" @click="queryText(index)" v-text="item"></div>
                    <div v-show="zwTitleArr.length>3" class="fj_open" @click="zwFlag=!zwFlag">{{zwFlag==true?'收起附件':'展开全部'}}</div>
                </div>
                <div class="fujian" v-show="textFlag==3" :class="{'abstract_hei':fjFlag==true}">
                    <div class="fjTitle" v-show="fjFlag==true?index<fjTitleArr.length:index<3" v-for="(item, index) in fjTitleArr" @click="queryText(zwTitleArr.length+index)" v-text="item"></div>
                    <div v-show="fjTitleArr.length>3" class="fj_open" @click="fjFlag=!fjFlag">{{fjFlag==true?'收起附件':'展开全部'}}</div>
                </div>
                <div class="initiate">
                    <yd-flexbox>
                        <div class="ini_left">发起方：</div>
                        <yd-flexbox-item class="ini_right" v-text="formData.reqdeptname"></yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="ini_left">联系人：</div>
                        <div class="ini_right" v-text="formData.requsername"></div>
                        <yd-flexbox-item class="number" v-text="formData.reqtel"></yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="ini_left">提交时间：</div>
                        <div class="ini_right" v-text="formData.reqstartdate"></div>
                    </yd-flexbox>
                </div>
                <div class="backSuggest" v-show="applysLength!=0||outersLength!=0||leadersLength!=0">
                    <div class="suggestTitle">
                        <div class="swiper-container">
                            <div class="swiper-wrapper">
                                <div class="swiper-slide" @click="sugNum=1;opinions=formData.leadersOpinions" :class="{active:sugNum==1}" v-show="leadersLength>0">公司领导意见</div>
                                <div class="swiper-slide" @click="sugNum=2;opinions=formData.outersOpinions" :class="{active:sugNum==2}" v-show="outersLength>0">部外审批意见</div>
                                <div class="swiper-slide" @click="sugNum=3;opinions=formData.applysOpinions" :class="{active:sugNum==3}" v-show="applysLength>0">主办方意见</div>
                            </div>
                        </div>
                    </div>
                    <div class="suggestText" v-for="(item, index) in opinions" :key="index">
                        <div class="name_sug">
                            <div class="sugLeft" v-if="item.userName1!=''">{{item.userName1}}：</div>
                            <div class="sugRight" v-if="item.userName1!=''" :style="{marginLeft:item.userName1.length*0.33+0.2+'rem'}" v-html="transferString(item.neirong)"></div>
                            <div class="sugRight" v-if="item.userName1==''" :style="{marginLeft:0+'rem'}" v-html="transferString(item.neirong)"></div>
                        </div>
                        <div class="depart_time">
                            <div class="dt_one">{{item.orgName}}</div>
                            <div class="dt_two" :class="{dt_one2:item.orgName==''}">{{getDay(item.submitDate)}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <yd-popup v-model="show" position="center" width="5.79rem" height="auto">
            <div class="other">其他意见</div>
            <yd-cell-group>
                <yd-cell-item>
                    <yd-textarea slot="right" placeholder="请您输入意见..." v-model="opinion"></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes" id="js_determineBtn" @click="show=false;leaderApprove()">确定</li>
                    <li class="no" id="js_cancelBtn" @click="show=false">取消</li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-if="step==1&&type==0">
            <div class="agree" @click="opinion='同意';leaderApprove()">同意</div>
            <div class="disagree" @click="opinion='不同意';leaderApprove()">不同意</div>
            <div class="otherAgree" @click="show=true">其他意见</div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="step==2&&type==0">
            <div class="agree" @click="$router.push('/asSecretary/'+workId)">发送</div>
            <div class="back" @click="returnAccept()">发送至主办方</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
// import axios from 'axios';
export default {
    data() {
        return {
            abstract: '',
            openFlag: false,
            textFlag: 1,
            fjTitleArr: [],
            zwTitleArr: [],
            fjFlag: false,
            zwFlag: false,
            sugNum: 1,
            show: false,
            formData: {},
            opinions: [],
            file_url: [],
            doKey: '',
            approveType: '',
            leaderIDs: '',
            opinion: '', // 领导审批意见
            appleaderId: '',
            sendReqLeaderId: '',
            sec2LeaderId: '',
            sec2LeaderName: '',
            approver: [],
            applysOpinions: '',
            outersOpinions: '',
            leadersOpinions: '',
            fuyanData: '',
            fuyan: '',
            applysLength: 0,
            outersLength: 0,
            leadersLength: 0,
            arrAbs: [],
            numT: 0,
            step: this.$route.params.step,
            workId: this.$route.params.workId,
            type: this.$route.params.type,
            errorTime: 0
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('业务审批');
        vm.queryForm();
        vm.errorTime = 0;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getHandleInfo', 'getSystemInfo'])
    },
    methods: {
        ...mapMutations([
            'setHandleInfo',
            'setMishuInfo',
            'setSystemInfo'
        ]),
        // swiper插件
        loadSwiper() {
            let vm = this;
            vm.$nextTick(() => {
                this.galleryTop = new Swiper('.swiper-container', {
                    initialSlide: 0,
                    slidesPerView: 'auto',
                    paginationClickable: true,
                    observer: true, // 修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true // 修改swiper的父元素时，自动初始化swiper
                })
            });
        },
        // 表单查询
        queryForm() {
            let vm = this;
            if (vm.getSystemInfo.jsonData != '' && vm.getSystemInfo.jsonData.workId == vm.$route.params.workId) {
                if (vm.getSystemInfo.fuyan != '' && vm.getSystemInfo.fuyan != '#none') {
                    vm.fuyanData = 'block';
                    vm.fuyan = vm.getSystemInfo.fuyan;
                }
                vm.formData = vm.getSystemInfo.jsonData;
                if (vm.formData.CURRENTSTATE != 10 && vm.$route.params.type == 0) {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.type == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.type == 0) {
                        return false;
                    }
                }
                vm.abstract = vm.formData.reqdesc;
                vm.opinions = vm.formData.leadersOpinions;
                vm.applysLength = vm.formData.applysOpinions.length;
                vm.outersLength = vm.formData.outersOpinions.length;
                vm.leadersLength = vm.formData.leadersOpinions.length;
                if (vm.leadersLength == 0 && vm.outersLength != 0) {
                    vm.sugNum = 2;
                    vm.opinions = vm.formData.outersOpinions;
                } else if (vm.leadersLength == 0 && vm.outersLength == 0 && vm.applysLength != 0) {
                    vm.sugNum = 3;
                    vm.opinions = vm.formData.applysOpinions;
                }
                vm.fjTitleArr = vm.formData.fjFile;
                vm.zwTitleArr = vm.formData.zwFile;
                // step==1是审批页面
                if (vm.step == 1) {
                    for (let key in vm.formData.nextBtn) {
                        if (key.indexOf('doApprove') != -1) {
                            vm.doKey = key;
                            vm.approveType = vm.formData.nextBtn.doApprove.type;
                            for (let keyId in vm.formData.nextBtn.doApprove) {
                                if (keyId != 'type') {
                                    vm.leaderIDs = keyId;
                                }
                            }
                        }
                        if (key.indexOf('secrataryAppActivity') != -1) {
                            vm.doKey = key;
                            vm.leaderIDs = vm.formData.nextBtn.secrataryAppActivity[0];
                        }
                    }
                }
                // step==2是秘书页面
                if (vm.step == 2) {
                    vm.setMishuInfo({
                        requsername: vm.formData.requsername,
                        reqdeptname: vm.formData.reqdeptname
                    })
                    for (let key in vm.formData.nextBtn) {
                        if (key.indexOf('leaderAppActivity') != -1) {
                            vm.appleaderId = vm.formData.nextBtn.leaderAppActivity[0];
                            vm.appleaderName = vm.formData.nextBtn.leaderAppActivity[1];
                            vm.setMishuInfo({
                                leaderId: vm.appleaderId,
                                leaderName: vm.appleaderName
                            })
                        }
                        if (key.indexOf('secrataryAppActivity2') != -1) {
                            vm.sec2LeaderId = vm.formData.nextBtn.secrataryAppActivity2[0];
                            vm.sec2LeaderName = vm.formData.nextBtn.secrataryAppActivity2[1];
                            vm.setMishuInfo({
                                sec2LeaderId: vm.sec2LeaderId,
                                sec2LeaderName: vm.sec2LeaderName
                            })
                        }
                        if (key.indexOf('sendReqActivity') != -1) {
                            vm.sendReqLeaderId = vm.formData.nextBtn.sendReqActivity[0];
                        }
                    }
                }
                vm.loadFile();
                vm.loadSwiper();
            } else {
                vm.$dialog.loading.open('加载中...');
                vm.getLast();
                var promise = new Promise(function(resolve, reject) {
                    vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                        'type': 'crossSystemApproval',
                        'workItemID': vm.workId,
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                    }, function(res) {
                        if (res.isSuccess == 'false') {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.queryForm();
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        } else if (res.isSuccess == 'true') {
                            vm.$dialog.loading.close();
                        }
                        if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.type == 0 && res.isSuccess == 'true') {
                            vm.$dialog.toast({
                                mes: '该流程已被处理',
                                timeout: 2000,
                                callback: () => {
                                    setTimeout(function() {
                                        if (vm.$route.params.type == 0) {
                                            vm.$router.push('/handle');
                                        }
                                    })
                                }
                            });
                            if (vm.$route.params.type == 0) {
                                return false;
                            }
                        }
                        res.jsonData.workId = vm.workId;
                        vm.setSystemInfo({
                            jsonData: res.jsonData
                        })
                        vm.formData = res.jsonData;
                        vm.abstract = res.jsonData.reqdesc;
                        vm.opinions = res.jsonData.leadersOpinions;
                        vm.applysLength = res.jsonData.applysOpinions.length;
                        vm.outersLength = res.jsonData.outersOpinions.length;
                        vm.leadersLength = res.jsonData.leadersOpinions.length;
                        if (vm.leadersLength == 0 && vm.outersLength != 0) {
                            vm.sugNum = 2;
                            vm.opinions = vm.formData.outersOpinions;
                        } else if (vm.leadersLength == 0 && vm.outersLength == 0 && vm.applysLength != 0) {
                            vm.sugNum = 3;
                            vm.opinions = vm.formData.applysOpinions;
                        }
                        vm.fjTitleArr = res.jsonData.fjFile;
                        vm.zwTitleArr = res.jsonData.zwFile;
                        // step==1是审批页面
                        if (vm.step == 1) {
                            for (let key in res.jsonData.nextBtn) {
                                if (key.indexOf('doApprove') != -1) {
                                    vm.doKey = key;
                                    vm.approveType = vm.formData.nextBtn.doApprove.type;
                                    for (let keyId in vm.formData.nextBtn.doApprove) {
                                        if (keyId != 'type') {
                                            vm.leaderIDs = keyId;
                                        }
                                    }
                                }
                                if (key.indexOf('secrataryAppActivity') != -1) {
                                    vm.doKey = key;
                                    vm.leaderIDs = res.jsonData.nextBtn.secrataryAppActivity[0];
                                }
                            }
                        }
                        // step==2是秘书页面
                        if (vm.step == 2) {
                            vm.setMishuInfo({
                                requsername: vm.formData.requsername,
                                reqdeptname: vm.formData.reqdeptname
                            })
                            for (let key in res.jsonData.nextBtn) {
                                if (key.indexOf('leaderAppActivity') != -1) {
                                    vm.appleaderId = res.jsonData.nextBtn.leaderAppActivity[0];
                                    vm.appleaderName = res.jsonData.nextBtn.leaderAppActivity[1];
                                    vm.setMishuInfo({
                                        leaderId: vm.appleaderId,
                                        leaderName: vm.appleaderName
                                    })
                                }
                                if (key.indexOf('secrataryAppActivity2') != -1) {
                                    vm.sec2LeaderId = res.jsonData.nextBtn.secrataryAppActivity2[0];
                                    vm.sec2LeaderName = res.jsonData.nextBtn.secrataryAppActivity2[1];
                                    vm.setMishuInfo({
                                        sec2LeaderId: vm.sec2LeaderId,
                                        sec2LeaderName: vm.sec2LeaderName
                                    })
                                }
                                if (key.indexOf('sendReqActivity') != -1) {
                                    vm.sendReqLeaderId = res.jsonData.nextBtn.sendReqActivity[0];
                                }
                            }
                        }
                        resolve(true);
                    }, function(res) {
                        if (res.response.status != 200) {
                            vm.errorTime++;
                            if (vm.errorTime < 3) {
                                vm.queryForm();
                                console.log(vm.errorTime);
                            } else {
                                vm.$dialog.loading.close();
                                vm.$dialog.toast({
                                    mes: '表单未加载成功',
                                    timeout: 2000,
                                    callback: () => {
                                        vm.$router.push('/handle');
                                    }
                                });
                            }
                        }
                    });
                });
                promise.then(function(data) {
                    if (data) {
                        vm.loadFile();
                        vm.loadSwiper();
                    }
                })
            }
        },
        // 上一则附言
        getLast() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'crossSystemApproval',
                'workItemID': vm.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                vm.fuyanData = res.jsonData;
                if (res.jsonData != '') {
                    vm.fuyan = res.jsonData[0].neirong;
                    vm.setSystemInfo({
                        fuyan: res.jsonData[0].neirong
                    });
                } else {
                    vm.setSystemInfo({
                        fuyan: '#none'
                    });
                }
            }, function(res) {}, false);
            // console.log(vm.getSystemInfo.fuyan != '');
            // if (vm.getSystemInfo.fuyan != '' && vm.getSystemInfo.fuyan != '#none') {
            //     vm.fuyanData = 'block';
            //     vm.fuyan = vm.getSystemInfo.fuyan;
            // } else {
            //     vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
            //         'type': 'crossSystemApproval',
            //         'workItemID': vm.workId,
            //         'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            //     }, function(res) {
            //         vm.fuyanData = res.jsonData;
            //         if (res.jsonData != '') {
            //             vm.fuyan = res.jsonData[0].neirong;
            //             vm.setSystemInfo({
            //                 fuyan: res.jsonData[0].neirong
            //             });
            //         } else {
            //             vm.setSystemInfo({
            //                 fuyan: '#none'
            //             });
            //         }
            //     });
            // }
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        },
        loadFile() {
            let vm = this;
            let qzfiles = [...vm.zwTitleArr, ...vm.fjTitleArr];
            qzfiles.forEach(function(item, index) {
                if (item.indexOf('.rar') != -1) {
                    vm.file_url[index] = '.rar';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else if (item.indexOf('.zip') != -1) {
                    vm.file_url[index] = '.zip';
                    vm.setHandleInfo({ file_url: vm.file_url });
                } else {
                    let item2 = item.replace(/ /g, '#1mk');
                    vm.$http.get('/portalMgmt/oa/menus/getPdf', {
                        params: {
                            'type': 'crossSystemApproval',
                            'fileName': item2,
                            'workItemID': vm.workId,
                            'resultType': 'queryAttachByWorkItemIDResult2',
                            'random': Math.floor(Math.random() * (100000 + 1))
                        }
                    }).then((res) => {
                        vm.file_url[index] = res.data.jsonData;
                        vm.setHandleInfo({ file_url: vm.file_url });
                    }).catch(error => {
                        // vm.$dialog.toast({
                        //     mes: '系统正在开小差，请稍后再试',
                        //     timeout: 1500
                        // });
                        console.log(error);
                    });
                }
            });
        },
        queryText(index) {
            var vm = this;
            if (vm.$route.params.type == 0) {
                let detailUrl = vm.getHandleInfo.file_url[index];
                if (!detailUrl) {
                    vm.$dialog.toast({
                        mes: '附件较大，正在加载中，请稍候',
                        timeout: 1500
                    });
                } else if (detailUrl == '.rar') {
                    vm.$dialog.toast({
                        mes: '暂不支持rar等压缩文件在线阅读',
                        timeout: 2000
                    });
                } else if (detailUrl == '.zip') {
                    vm.$dialog.toast({
                        mes: '暂不支持zip等压缩文件在线阅读',
                        timeout: 2000
                    });
                } else {
                    vm.page_jump(detailUrl);
                }
            }
        },
        page_jump(detailUrl, isImg = 'notImg') {
            const vm = this;
            if (detailUrl.fileType != '.pdf') {
                let index1 = detailUrl.pdfUrl.lastIndexOf('.') + 1;
                let index2 = detailUrl.pdfUrl.length;
                let txtName = detailUrl.pdfUrl.substring(index1, index2);
                if (txtName != 'pdf') {
                    window.location.href = detailUrl.pdfUrl;
                } else {
                    var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                    window.location.href = urls;
                }
                // var urls = vm.isIOS == true ? (detailUrl.pdfUrl.substring(0, detailUrl.pdfUrl.lastIndexOf('.')) + detailUrl.fileType) : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                // window.location.href = urls;
            } else {
                var urls2 = vm.isIOS == true ? detailUrl.pdfUrl : '/pdfjs/web/viewer.html?file=' + detailUrl.pdfUrl;
                window.location.href = urls2;
            }
        },
        // 领导审批按钮
        leaderApprove() {
            let vm = this;
            console.log(vm.doKey)
            if (vm.doKey == 'doApprove') {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'crossSystemApproval',
                    'approveType': vm.approveType,
                    'workItemID': vm.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.reqdeptname + vm.formData.requsername + '业务审批',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                console.log(2);
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'crossSystemApproval',
                    'workItemID': vm.workId,
                    'activityID': vm.doKey,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.formData.reqdeptname + vm.formData.requsername + '业务审批',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        // 退回受理方
        returnAccept() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'crossSystemApproval',
                'workItemID': vm.workId,
                'activityID': 'sendReqActivity',
                'leaderIDs': vm.sendReqLeaderId,
                'fuyan': '',
                'opinion': '',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'workTitle': vm.formData.reqdeptname + vm.formData.requsername + '业务审批',
                'ext': ''
            }, function(res) {
                vm.$dialog.toast({
                    mes: '审批完成',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle/');
                    }
                });
            })
        }
    }
}
</script>
<style lang="less" scoped>
@import './acrossSystem.less';

.dt_one2 {
    margin-left: 0 !important;
}

.yd-cell-box {
    margin-bottom: 0;
}

.otherBtn {
    overflow: hidden;
}
</style>


